<template>
  <div>
    <app-header-row :item="service"></app-header-row>
    <app-alert :alert="hasError" @closeAlert="hasError = false">
      {{ error }}
    </app-alert>
    <v-form v-model="isValid">
      <v-data-table
        v-if="meteres"
        :headers="meteres.headers"
        :items="meteres.items"
        hide-default-footer
        item-key="idRow"
        disable-filtering
        disable-sort
      >
        <template v-slot:item.newIndication="{ item }">
          <v-text-field
            :value="item.newIndication"
            v-model="devices[item.id]"
            :rules="[rules.required, rules.number, rules.minValue(item.indication)]"
          ></v-text-field>
        </template>
      </v-data-table>
      <v-row justify="end">
        <v-btn
          :disabled="!isValid"
          @click="flushMeteresClick"
          class="mt-2"
        >
          Передать показания
        </v-btn>
      </v-row>
    </v-form>
    <app-alert class="mt-5" :alert="flushMeteresResult && showResult" @closeAlert="showResult = false" :color="'success'">
      Показания переданы
    </app-alert>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import Alert from '../blocks/Alert'
import HeaderRow from './HeaderRow'

export default {
  name: 'Meteres',
  props: {
    service: { required: true }
  },
  data: () => ({
    hasError: false,
    isValid: true,
    showResult: true,
    devices: {},
    rules: {
      required: value => !!value || 'Заполните значение.',
      number: value => /^\d+$/.test(value) || 'Введите число',
      minValue (min) {
        return value => value >= min || 'Передача показаний меньше предыдущих невозможна. Обратитесь в управление по реализации услуг!'
      }
    }
  }),
  components: {
    appAlert: Alert,
    appHeaderRow: HeaderRow
  },
  methods: {
    ...mapActions('personalCommon', {
      getMeteres: 'getMeteres',
      flushMeteres: 'flushMeteres'
    }),
    ...mapActions(['show_preloader']),
    flushMeteresClick () {
      this.flushMeteres(this.devices)
    }
  },
  computed: {
    ...mapGetters('personalCommon', {
      meteres: 'meteres',
      error: 'error',
      flushMeteresResult: 'flushMeteresResult'
    })
  },
  watch: {
    error (val) {
      this.hasError = Boolean(val)
    },
    meteres (val) {
      val.items.map(item => {
        this.devices[item.id] = item.newIndication
      })
    }
  },
  created () {
    this.show_preloader()
    this.getMeteres()
    this.hasError = Boolean(this.error)
  }
}
</script>
